"use client";

import {
  Facebook,
  Instagram,
  Mail,
  MapPin,
  Phone,
  Twitter,
} from "lucide-react";

export default function Contact() {
  return (
    <div className="pt-6 md:pt-20">
      <section className="py-16 px-6 md:px-12">
        <div className="mb-8 text-center md:text-left">
          <h2 className="text-2xl md:text-4xl font-semibold tracking-tight text-gray-900">
            Contact Us
          </h2>
          <p className="text-gray-600 mt-2">
            Have any questions? We'd love to hear from you.
          </p>
        </div>

        <div className="grid md:grid-cols-2 gap-10 items-start">
          {/* FORM KIRI */}
          <form className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Name
              </label>
              <input
                type="text"
                placeholder="Your Name"
                className="w-full border border-gray-300 rounded-lg px-4 py-2 focus:ring-2 focus:ring-gray-800 outline-none"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Email
              </label>
              <input
                type="email"
                placeholder="you@example.com"
                className="w-full border border-gray-300 rounded-lg px-4 py-2 focus:ring-2 focus:ring-gray-800 outline-none"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Message
              </label>
              <textarea
                placeholder="Write your message..."
                rows={5}
                className="w-full border border-gray-300 rounded-lg px-4 py-2 focus:ring-2 focus:ring-gray-800 outline-none resize-none"
              />
            </div>

            <button
              type="submit"
              className="bg-gray-900 text-white px-6 py-2 rounded-lg hover:bg-gray-800 transition-colors"
            >
              Send Message
            </button>
          </form>

          {/* KANAN: MAP + SOSIAL MEDIA */}
          <div>
            <div className="w-full h-72 md:h-96 overflow-hidden rounded-xl shadow-md">
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3958.997020937645!2d110.37500617453215!3d-7.820874477932364!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x2e7a57b7cfb34c67%3A0xd7d3c88974c7b31!2sYogyakarta!5e0!3m2!1sen!2sid!4v1695393315412!5m2!1sen!2sid"
                width="100%"
                height="100%"
                loading="lazy"
                className="border-0"
              ></iframe>
            </div>

            {/* Social Media Icons */}
            <div className="flex justify-center md:justify-start gap-5 mt-6 text-gray-600">
              <a href="#" className="hover:text-gray-900 transition-colors">
                <Facebook size={24} />
              </a>
              <a href="#" className="hover:text-gray-900 transition-colors">
                <Instagram size={24} />
              </a>
              <a href="#" className="hover:text-gray-900 transition-colors">
                <Twitter size={24} />
              </a>
              <a href="#" className="hover:text-gray-900 transition-colors">
                <Mail size={24} />
              </a>
              <a href="#" className="hover:text-gray-900 transition-colors">
                <Phone size={24} />
              </a>
              <a href="#" className="hover:text-gray-900 transition-colors">
                <MapPin size={24} />
              </a>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}
